<template>
    <div class="slogan-box" :class="isFixed?'fix-banner':''">
        <div class="logo-box">
            <div class="slogan">
                <img src="./../assets/img/home/logo.png" alt="" class="logo">
                    <div class="school">
                        <p>北方教育研发中心</p>
                        <p class="en-school">NORTH EDUCATION RESEARCH AND DEVELOPMENT CENTER</p>
                    </div>
                </div>
            <img src="./../assets/img/home/menu.png" alt="" class="menu" @click.stop="showMoreMenu">
        </div>
        <div ref="modal" class="modal" v-show="is_show_modal" :class="show_more_menu?'rotate-in':'rotate-out'">
            <p @click.stop="toHome">
                <span>首页</span>
                <span>></span>
            </p>
            <p @click.stop="toAboutus">
                <span>公司简介</span>
                <span>></span>
            </p>
            <p @click.stop="toContact">
                <span>联系我们</span>
                <span>></span>
            </p>
            <p @click.stop="toFeedback">
                <span>意见反馈</span>
                <span>></span>
            </p>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            isFixed: {
                type: Boolean,
                required: false,
                default: true
            }
        },
        data () {
            return {
                show_more_menu: false,
                is_show_modal: false
            }
        },
        mounted () {
            // 监听动画结束再隐藏
            let that = this;
            this.$refs.modal.addEventListener('webkitAnimationEnd',function () {
                if(!that.show_more_menu) {
                    that.is_show_modal = false;
                }
            })
        },
        methods: {
            showMoreMenu () {
                this.show_more_menu = !this.show_more_menu;
                if(this.show_more_menu) {
                    this.is_show_modal = true;
                }
            },
            toHome () {
                this.$router.push({
                    path: '/main/home'
                })
            },
            toFeedback () {
                this.$router.push({
                    path: '/feedback'
                })
            },
            toContact () {
                this.$router.push({
                    path: '/contact'
                })
            },
            toAboutus () {
                this.$router.push({
                    path: '/aboutus'
                })
            }
        },
    }
</script>

<style scoped>
    .slogan-box {
        width: 7.1rem;
        height: 1.4rem;
        padding: 0 0.2rem;
        background-color: #0096e3;
        position: relative;
        display: -webkit-flex;
        display: flex;
        align-items: center;
    }
    .slogan-box.fix-banner {
        position: fixed;
        top: 0;
        left: 0;
        z-index: 999;
    }
    .logo-box {
        width: 100%;
        height: 1.12rem;
