        color: #fff;
        display: -webkit-flex;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .logo-box .slogan {
        display: -webkit-flex;
        display: flex;
        align-items: center;
        font-size: 0.36rem;
    }
    .logo-box .slogan img {
        display: block;
        width: 0.74rem;
        margin-right: 0.26rem;
    }
    .logo-box .slogan .en-school {
        font-size: 0.14rem;
        margin-top: 0.1rem;
    }
    .menu {
        display: block;
        width: 0.48rem;
    }
    .modal {
        width: 7.5rem;
        position: absolute;
        top: 1.4rem;
        right: 0;
        background-color: #fff;
        z-index: 2;
        padding: 0.2rem 0;
    }
    .modal p {
        height: 0.82rem;
        line-height: 0.82rem;
        font-size: 0.26rem;
        text-align: left;
        color: #000;
        border-bottom: 0.02rem solid #eee;
        padding: 0 0.4rem;
        display: -webkit-flex;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .rotate-in {
        -webkit-animation: slideInRight 0.2s ease-in forwards;
        animation: slideInRight 0.2s ease-in forwards;
    }
    .rotate-out {
        -webkit-animation: slideOutRight 0.2s ease-out forwards;
        animation: slideOutRight 0.2s ease-out forwards;
    }
    @-webkit-keyframes slideInRight{0%{-webkit-transform:translate3d(100%,0,0);transform:translate3d(100%,0,0);visibility:visible}to{-webkit-transform:translateZ(0);transform:translateZ(0)}}
    @keyframes slideInRight{0%{-webkit-transform:translate3d(100%,0,0);transform:translate3d(100%,0,0);visibility:visible}to{-webkit-transform:translateZ(0);transform:translateZ(0)}}
    @-webkit-keyframes slideOutRight{0%{-webkit-transform:translateZ(0);transform:translateZ(0)}to{visibility:hidden;-webkit-transform:translate3d(100%,0,0);transform:translate3d(100%,0,0)}}
    @keyframes slideOutRight{0%{-webkit-transform:translateZ(0);transform:translateZ(0)}to{visibility:hidden;-webkit-transform:translate3d(100%,0,0);transform:translate3d(100%,0,0)}}
</style>