                            v-for="(item,index) in category_list" 
                            :key="item.id" 
                            :class="[current_swiper_index == index ? 'active':'']" 
                            @click="changeTab(index, item.id)">{{item.category_name}}</div>
                        <div class="tab"></div>
                    </div>
                </div>
                <div class="more-category" @click="showMoreCategory">
