        <div ref="modal" class="category-modal" :style="{'height':more_category_height}" v-show="show_more_category" :class="modal_animation?'modal-show-animation':'modal-hide-animation'">
            <div class="modal-inner">
                <div class="close-modal">
                    <img @click="closeCategoryModal" src="./../assets/img/category_back.jpg" alt="">
                </div>
                <div class="cate-box">
                    <div class="title">产品分类</div>
                    <div class="cates">
                        <div class="cate-item"
                            v-for="(item,index) in category_list" 
                            :key="item.id" 
                            :class="[current_swiper_index == index ? 'active':'']" 
