<script>
    import Swiper from 'swiper';
    import slogan from '@/components/slogan.vue';
    import company from '@/components/company.vue';

    import {anliCategory, anliList} from '@/request/api.js'

    export default {
        components : {
            slogan,
            company
        },
        data () {
            return {
                mySwiper: null,
                current_swiper_index: 0,
                // 更多分类弹窗
                show_more_category: false,
                // 弹窗高度
                more_category_height: 0,
                modal_animation: false,
                // 分类
                category_list: [],
                product_list: [],
            }
        },
        created () {
            this.anliCategory();
        },
        mounted () {
            let that = this;
            // 实例swiper
            this.$nextTick(function () {
                this.mySwiper = new Swiper ('.swiper-container', {
                    autoHeight: true,
                    observer:true,//修改swiper自己或子元素时，自动初始化swiper 
                    observeParents:false,//修改swiper的父元素时，自动初始化swiper
                    on:{
                        transitionEnd: function(){
                            that.current_swiper_index = this.realIndex;
                        }
                    }
                });
            })
            // 弹窗全屏高度
            let page_height = window.screen.height;
            this.more_category_height = page_height + 'px';

            // 监听动画结束再隐藏
            this.$refs.modal.addEventListener('webkitAnimationEnd',function () {
                if(!that.modal_animation) {
                    that.show_more_category = false;
                }
            })
        },
        methods: {
            anliCategory () {
                let that = this;
                anliCategory({}).then(res => {
                    console.log(res)
                    if(res.code == 200) {
                        that.category_list = res.data;
                        // let result = res.data;

                        // // 保证接口返回顺序用promise.all()
                        // let queue = [];
                        // for(var i = 0, len = result.length; i < len; i++) {
                        //     queue.push(this.anliList(result[i].id))
                        // }
                        // Promise.all(queue).then(res => {
                        //     res.forEach(item => {
                        //         that.product_list.push(item)
                        //     })
                        // })

                    }
                })
            },
            // 产品列表
            anliList (category_id) {
                let that = this;
                let data = {
                    category_id: category_id
                }
                // 因为通过遍历循环调用此接口，所以需要用promise.all()方法保证按先后顺序执行，所以将promise对象作为返回值
                return new Promise(resolve => {
                    anliList(data).then(res => {
                        resolve(res.data)
                    })
                })
            },
            changeTab (new_index) {
                this.current_swiper_index = new_index;
                // 调用swiper滚动的方法
                this.mySwiper.slideTo(new_index, 400, false);
                // this.show_more_category = false;
                this.modal_animation = false;
            },
            showMoreCategory () {
                this.modal_animation = true;
                this.show_more_category = true;
            },
            closeCategoryModal () {
                this.modal_animation = false;
            }
        }
    }
</script>

